<script>
import { mapState, mapActions } from 'vuex'
import FreetextOutlined from '@/components/freetext/Outlined.vue'
// import SwitchLang from '@/components/layouts/SwitchLang.vue'
import Install from '@/components/modals/Install.vue'
import store from '@/store'

export default {
  name: 'App',
  components: {
    FreetextOutlined, Install// SwitchLang
  },
  data: () => ({
    activeId: 'Profile',
    value: 0,
    isMobile: false,
    isIpad: false,
    sheet: false,
    scrollNum: 0,
    deferredPrompt: null,
    isPWA: false
  }),
  computed: {
    ...mapState('home', {
      menus: (state) => state.menus,
      modal: (state) => state.modal,
      install: (state) => state.install
    }),
    ...mapState('auth', {
      state: (state) => state
    })
  },
  mounted () {
    window.addEventListener('beforeinstallprompt', (e) => {
      // Mencegah prompt default
      e.preventDefault()
      // Menyimpan event untuk digunakan nanti
      this.deferredPrompt = e
      if (this.deferredPrompt) {
        store.dispatch('home/install', true)
      }
    })
    const theme = localStorage.getItem('dark_theme')
    if (theme) {
      if (theme === 'true') {
        this.$vuetify.theme.dark = true
      } else {
        this.$vuetify.theme.dark = false
      }
    } else {
      this.$vuetify.theme.dark = false
      localStorage.setItem('dark_theme', this.$vuetify.theme.dark.toString())
    }
    this.isMobile = window.innerWidth < 600
    this.isIpad = window.innerWidth < 900
    window.addEventListener('resize', this.checkIsMobile)
    window.addEventListener('resize', this.checkIsIpad)
    window.addEventListener('scroll', this.handleScroll)
    this.checkPWAStatus()
  },
  filters: {
    upper (value) {
      if (!value) return ''
      value = value.toString()
      return value.toUpperCase()
    }
  },
  methods: {
    ...mapActions('auth', [
      'dialogFormLogin'
    ]),
    checkPWAStatus () {
      // Cek untuk browser modern
      if (window.matchMedia('(display-mode: standalone)').matches) {
        this.isPWA = true
      } else if (window.navigator.standalone === true) {
        this.isPWA = true
      } else {
        this.isPWA = false
        store.dispatch('home/bukaapk')
      }
    },
    installPWA () {
      if (this.deferredPrompt) {
        this.deferredPrompt.prompt()
        this.deferredPrompt.userChoice.then((choiceResult) => {
          if (choiceResult.outcome === 'accepted') {
            store.dispatch('home/install', false)
            console.log('User accepted the install prompt')
          } else {
            console.log('User dismissed the install prompt')
          }
          this.deferredPrompt = null
        })
      }
    },
    openPWA () {
      // if (window.matchMedia('(display-mode: standalone)').matches || window.navigator.standalone === true) {
      //   // Aplikasi berjalan dalam mode standalone (PWA sudah terbuka)
      //   console.log('PWA sudah terbuka.')
      // } else {
      //   // Buka PWA dari halaman web
      //   window.open(window.location.href, '_blank')
      // }
      const linkToShare = window.location.href // Ganti dengan link yang ingin dibuka

      if (navigator.standalone) {
        // Jika di iOS homescreen, buka di window sendiri
        window.location.href = linkToShare
      } else if (window.matchMedia('(display-mode: standalone)').matches) {
        // Jika di Android homescreen, buka di window sendiri
        window.location.href = linkToShare
      } else {
        // Buka di tab baru jika di browser
        window.open(linkToShare, '_blank')
      }
    },
    lightbulb () {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      localStorage.setItem('dark_theme', this.$vuetify.theme.dark.toString())
    },
    getContact () {
      const editButtonRef = this.$refs.contact
      editButtonRef.focus()
    },
    checkIsMobile () {
      this.isMobile = window.innerWidth < 600
    },
    checkIsIpad () {
      this.isIpad = window.innerWidth < 868
    },
    handleScroll () {
      // Mendapatkan posisi scroll saat ini
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop
      this.scrollNum = scrollTop
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.checkIsMobile)
    window.removeEventListener('resize', this.checkIsIpad)
  }
}
</script>
