<template>
  <v-container fill-height fluid>
    <v-row justify="center">
      <v-dialog
        v-model="state.modal"
        persistent
        max-width="420px"
      >
        <v-card justify="center" class="pa-6">
          <v-row justify="center">
            <v-btn v-if="state.install" @click="installApk()">INSTALL APK</v-btn> <v-btn v-else-if="state.apk" @click="openApk()">RUN APK</v-btn>
          </v-row>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>
<script>
import { mapActions, mapState } from 'vuex'

export default {
  name: 'ModalInstall',
  data: () => ({
    valid: true,
    show: false
  }),
  computed: {
    ...mapState('home', {
      state: (state) => state
    })
  },
  filters: {
    upper (value) {
      if (!value) return ''
      value = value.toString()
      return value.toUpperCase()
    }
  },
  methods: {
    ...mapActions('auth', [
      'reqCaptchaNew'
    ]),
    installApk () {
      this.$emit('installApk')
    },
    openApk () {
      this.$emit('openApk')
    },
    onCaptchaVerified (response) {
      if (response) this.robot = true
    },
    onCaptchaExpired: function () {
      this.$refs.recaptcha.reset()
    },
    validate () {
      this.$refs.form.validate()
      this.$store.dispatch('auth/validate')
    },
    reset () {
      this.$refs.form.reset()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    }
  }
}
</script>
<style>
.g-recaptcha{
  transform: none !important
}
</style>
